<template>
    <div class="wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!---->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">{{fold?'':''}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="">
                            <el-input v-model="searchForm.orderCode" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.waybillNo" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.packageCode" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.packageStatus" size="small" clearable>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.packageStatus" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.supplierCode" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.settlementCode" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.status" size="small" clearable>
                                <el-option value="" label=""></el-option>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.status" :key="item.val" ></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.importFreightTimeStart" :picker-options="pickerOptions" :format="'yyyy-MM-dd'"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.importFreightTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.paymentTimeStart" :picker-options="pickerOptions" :format="'yyyy-MM-dd'"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.paymentTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt"></el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i></el-button>
                        </el-form-item>
                    </div>
                </transition>
            </el-form>
        </div>
        <div class="import">
            <el-upload class="upload-import" action="/proxy/report/api/import/financial/freight" :on-success="doImport" :accept="'file/file.xls'" :file-list="fileList">
                <el-button size="small" type="primary"></el-button>
            </el-upload>
        </div>
        <!---->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" highlight-current-row border>
            <el-table-column label="" prop="orderCode" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="waybillNo" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="supplierCode" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="packageCode" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="packageStatus" align="center" width="180" :formatter="format_status" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="status" align="center" width="180" :formatter="format_status" show-overflow-tooltip></el-table-column>
            <el-table-column label="" align="center" width="160">
                <template slot-scope="scope">{{scope.row.paymentTime|dateFormat }}</template>
            </el-table-column>
            
            
            <el-table-column label="" width="100" align="right">
                <template slot-scope="scope">{{(scope.row.orderAmount/100).toFixed(2)}}</template>
            </el-table-column>
            
            <el-table-column label="" width="160" align="center">
                <template slot-scope="scope">{{scope.row.isOutWaybill==0?'':''}}</template>
            </el-table-column>
            <el-table-column label="" width="130" align="right">
                <template slot-scope="scope">{{(scope.row.totalPostage/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" width="120" align="right">
                <template slot-scope="scope">{{(scope.row.discountPostage/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" width="120" align="right">
                <template slot-scope="scope">{{(scope.row.postage/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" prop="settlementCode" align="center" width="180"></el-table-column>
            <el-table-column label="" align="center" width="160">
                <template slot-scope="scope">{{scope.row.settlementTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="(kg)" prop="feeWeight" align="center" width="180">
                <template slot-scope="scope">{{scope.row.feeWeight?(scope.row.feeWeight/1000):''}}</template>
            </el-table-column>
            <el-table-column label="" prop="postageProductType" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="recipientArea" align="center" width="180"></el-table-column>
            <el-table-column label="" align="center" width="160">
                <template slot-scope="scope">{{scope.row.importFreightTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="" align="center" width="160">
                <template slot-scope="scope">{{scope.row.buildTime|dateFormat }}</template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>


<script>
import format from 'date-format';
export default {
    data() {
        return {
            //
            fold: true,
            searchForm: {
                orderCode: '',
                waybillNo: '',
                supplierCode: '',
                settlementCode: '',
                status: '',
                paymentTimeStart: '',
                paymentTimeEnd: '',
                packageCode:'',
                packageStatus:'',
                importFreightTimeStart:'',
                importFreightTimeEnd:'',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },
            pickerOptions: {
                disabledDate(time) {
                    if (time.getTime() > Date.now()) {
                        return time
                    } else {
                        return false;
                    }
                }
            },

            //
            loading: false,
            total: 0,
            reportList: [],
            fileList: [],
        }
    },
    filters: {
        dateFormat(date) {
            return date ? format('yyyy-MM-dd hh:mm', new Date(date)) : '';
        }
    },
    methods: {
        //
        format_status(row, column, cellValue) {
            for (let i in this.myConfig.status[column.property]) {
                if (this.myConfig.status[column.property][i].val == row[column.property]) {
                    return this.myConfig.status[column.property][i].label;
                    break;
                }
            }
            return row[column.property];
        },
        //
        DateToString() {
            for (let i in this.searchForm) {
                //
                if (this.searchForm[i] instanceof Date) {
                    this.searchForm[i] = format('yyyy-MM-dd', new Date(this.searchForm[i]));
                }
                //
                if (this.searchForm[i] instanceof String) {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
        },
        search_smt() {
            this.searchForm.page.page = 1;
            this.DateToString();
            this.getReportList();
        },
        //
        getReportList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/report/api/report/financial/freight', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.reportList = data.body.rows;
                    this.total = data.body.total;
                }
            })
        },
        //
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getReportList();
        },

        //
        export_smt() {
            this.DateToString();
            var tmp = JSON.parse(JSON.stringify(this.searchForm));
            delete tmp.page;
            function serialize(obj) {
                var str = [];
                for (var p in obj)
                    if (obj.hasOwnProperty(p) && obj[p] != "") {
                        if (obj[p] instanceof Array) {
                            str.push(p + "Str=" + obj[p].join());
                        } else {
                            str.push(p + "=" + obj[p]);
                        }
                    }
                return str.join("&");
            }
             window.open('/proxy/report/api/export/financial/freight?' + serialize(tmp));
        },
        //
        doImport(res, file, fileList) {
            if (res.head.code == "10000") {
                this.$message({
                    type: 'success',
                    message: ''
                })
            } else {
                for (let i in fileList) {
                    if (file.uid == fileList[i].uid) {
                        fileList.splice(i, 1);
                    }
                }
                console.log(res,3333333)
                let _html=res.body.sub_msg;
                this.$alert(`<div>${_html}</div>`, '', {
                    dangerouslyUseHTMLString: true,
                    customClass:'message-import'
                });
                
            }
            this.getReportList();
        }


    },
    created() {
        this.getReportList();
    }
}
</script>


<style lang="less" >
.wrap {
    padding-right: 40px;
    
}
.message-import .el-message-box__message{
     
    max-height: 300px;
    overflow-y: auto;
}
</style>



