<template>
    <div class="wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!---->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">{{fold?'':''}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="">
                            <el-date-picker type="datetime" placeholder="" v-model="searchForm.orderTimeStart" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="" class="">
                            <el-date-picker type="datetime" placeholder="" v-model="searchForm.orderTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.buyerEmail" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.orderCode" size="small" type="textarea" rows="1"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.sourceOrderCode" size="small" type="textarea" rows="1"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.waybillNo" size="small" type="textarea" rows="1"></el-input>
                        </el-form-item>
                        
                        <el-form-item label="">
                            <el-input v-model="searchForm.packageCode" size="small" type="textarea" rows="1"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.supplierCode" size="small" type="textarea" rows="1"></el-input>
                        </el-form-item>
                        
                        <el-form-item label="">
                            <el-select v-model="searchForm.orderSource" size="small" multiple filterable clearable>
                                <el-option :value="item.code" :label="item.name" v-for="item in cpaList" :key="item.code"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.orderStatus" size="small" multiple filterable clearable>        
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.orderStatus" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.packageStatus" size="small" multiple filterable clearable>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.packageStatus" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.logisticsStatus" size="small" multiple clearable filterable>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.logisticsStatus" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.logisticsCode" size="small" multiple filterable clearable>                                
                                <el-option :value="item.logisticsCode" :label="item.logisticsName" v-for="item in logisticsList" :key="item.logisticsCode"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.isErrorShiped" size="small"  clearable>
                                <el-option value="1" label="" ></el-option>
                                <el-option value="0" label="" ></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.signType" size="small"  clearable>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.signType" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>

                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt"></el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>
                            </el-button>

                        </el-form-item>

                    </div>
                </transition>

            </el-form>
            <div class="form-download" style="display:none;">
                <iframe src="" frameborder="0" name="msg-iframe" ></iframe>
                <form action="/proxy/report/api/export/customer/logistics" method="post" id="form-download" style="display:none;" target="msg-iframe">
                    <input type="text" name="data"  id="form-data" :value="downloadkey"/>
                    <button type="submit" >submit</button>
                </form>         
            </div> 


        </div>
        <!---->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" :tooltip-effect="'light'" highlight-current-row border>
            <el-table-column label="" prop="orderCode" align="center" width="150" show-overflow-tooltip :fixed="'left'"></el-table-column>
            <el-table-column label="" align="center" width="160">
                <template slot-scope="scope">{{scope.row.orderTime|dateFormat }}</template>
            </el-table-column>      
            <el-table-column label="" prop="packageCode" align="center" width="150" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="supplierCode" align="center" width="120" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="waybillNo" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="addTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.orderPayTime|dateFormat }}</template>
            </el-table-column> 
            <el-table-column label=""  align="center" width="160">
                <template slot-scope="scope">{{scope.row.orderCreateTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="" prop="orderStatus" align="center" width="100" :formatter="format_status"></el-table-column>
            <el-table-column label="" prop="packageStatus" align="center" width="120" show-overflow-tooltip :formatter="format_status"></el-table-column>
            <el-table-column label="SKU" prop="productSn" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="agentWaybillNo" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="isOutWaybill" align="center" width="150" :formatter="format_status"> </el-table-column>     
            <el-table-column label="" prop="logisticsName" align="center" width="120" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="insideWarehouse" align="center" width="120" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="insideLogisticsCode" align="center" width="100" :formatter="format_status"></el-table-column>
            <el-table-column label="" prop="insideWaybillNo" align="center" width="120" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="packagePrice" align="right" width="120" show-overflow-tooltip>
                <template slot-scope="scope">{{scope.row.packagePrice/100}}</template>
            </el-table-column>
            <el-table-column label="" prop="orderSourceCode" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="payMethod" align="center" width="120" show-overflow-tooltip :formatter="format_status"></el-table-column>
            <el-table-column label="" prop="sourceOrderCode" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label=" " prop="logisticsStatus" align="center" width="120" show-overflow-tooltip :formatter="format_status"></el-table-column>
            <el-table-column label="" align="center" width="160">
                <template slot-scope="scope">{{scope.row.deliveryTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="" align="center" width="160">
                <template slot-scope="scope">{{scope.row.fristLogisticsTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="" prop="waybillNo" align="center" width="100" >
                <template slot-scope="scope">{{scope.row.fristLogisticsTime?(scope.row.orderCreateTime>scope.row.fristLogisticsTime?'':''):''}}</template>
            </el-table-column>
            <el-table-column label="" prop="lastLogisticsNode" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" align="center" width="160">
                <template slot-scope="scope">{{scope.row.lastLogisticsTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="" align="center" width="160">
                <template slot-scope="scope">{{scope.row.signTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="" prop="signType" align="center" width="100" :formatter="format_status" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="logisticsTotalDuration" align="center" width="100" show-overflow-tooltip></el-table-column>

            <el-table-column label="SKU" prop="productName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="buyerEmail" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" align="center" width="160">
                <template slot-scope="scope">{{scope.row.orderUpdateTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="" prop="extendsInfo" align="center" width="180" show-overflow-tooltip>
            </el-table-column>
            <el-table-column label="" prop="cancelReason" align="center" width="180" show-overflow-tooltip>
            </el-table-column>
            <el-table-column label="" prop="remark" align="center" width="180" show-overflow-tooltip :fixed="'right'">
            </el-table-column>
            <el-table-column label="" align="left" width="120" :fixed="'right'">
                <template slot-scope="scope">                   
                    <el-button type="primary" size="mini" @click="doAction(scope.row)"></el-button>  
                </template>
            </el-table-column>

        </el-table>
        <!--  -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
        <el-dialog title="" :visible.sync="dialogRemark" class="add-menu-form" :close-on-click-modal="false" size="tinny">
            <el-form :model="remarkFrom" label-width="80px">
                <el-form-item label="" prop="menuIco">
                    <el-select v-model="remarkFrom.cancelReason" placeholder="" size="small" clearable >
                        <el-option :value="item" :label="item" v-for="item in cancelReason" :key="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="">
                    <el-input v-model="remarkFrom.remark" type="textarea"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogRemark = false"> </el-button>
                <el-button type="primary" @click="submit_remark"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>
<style lang="less" >
.wrap {
  padding-right: 40px;
  .el-textarea{
      vertical-align: middle;
      .el-textarea__inner{
        min-height: 31px !important;
        padding:2px 10px;
      }
  }
}
</style>

<script>
import format from "date-format";
export default {
  data() {
    return {
      //
      fold: true,
      searchForm: {
        orderCode: "",
        packageCode: "",
        orderTimeStart: "",
        orderTimeEnd: "",     
        supplierCode: "",
        waybillNo: "",
        orderSourceCode: "",
        buyerEmail: "",
        packageStatus: [],
        orderSource: [],
        logisticsCode: [],
        logisticsStatus: [],
        orderStatus:[],
        signType:'',
        isErrorShiped:'',
        page: {
          page: 1,
          pageSize: this.myConfig.pageRows
        }
      },
      pickerOptions: {
        disabledDate(time) {
          if (time.getTime() > Date.now()) {
            return time;
          } else {
            return false;
          }
        }
      },
      //
      loading: false,
      total: 0,
      reportList: [],
      // 
      cpaList: [],
      //
      logisticsList: [],

      //
      dialogRemark: false,
      remarkFrom: {
        remark: "",
        cancelReason: "",
        primarkIds: []
      },
      cancelReason: ["", "", "", "", ""]
    };
  },
  computed:{
      downloadkey(){
        return JSON.stringify(this.searchForm)
      }           
  },
  filters: {
    dateFormat(date) {
      return date ? format("yyyy-MM-dd hh:mm:ss", new Date(date)) : "";
    }
  },
  methods: {
    //
    format_status(row, column, cellValue) {
       
      for (let i in this.myConfig.status[column.property]) {
        if (this.myConfig.status[column.property][i].val == row[column.property]) {
            
          return this.myConfig.status[column.property][i].label;
          break;
        }
      }

      return row[column.property];
    },
    search_smt() {
      this.searchForm.page.page = 1;
      this.DateToString();
      this.getReportList();
    },
    //
    DateToString() {
      for (let i in this.searchForm) {
        //
        if (this.searchForm[i] instanceof Date) this.searchForm[i] = format("yyyy-MM-dd hh:mm:ss",new Date(this.searchForm[i])); 
        //
        if (this.searchForm[i] instanceof String) this.searchForm[i] = this.searchForm[i].trim();
        
      }
    },
    //
    getReportList() {
      this.loading = true;
      this.DateToString();
      this.axios
        .post(
          this.myConfig.code.proxy + "/report/api/report/customer/logistics",
          { body: this.searchForm }
        )
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.loading = false;
            this.reportList = data.body.rows;
            this.total = data.body.total;
          }
        });
    },
    //
    handleCurrentChange(val) {
      this.searchForm.page.page = val;
      this.getReportList();
    },
    //cpa
    getCpaList() {
      this.axios
        .post(this.myConfig.code.proxy + "/ec/cpa/partners/all", { body: {} })
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.cpaList = data.body.list;
          }
        });
    },
    // 
    getLogistics() {
      this.axios
        .post(this.myConfig.code.proxy + "/logistics/logistics/queryType", {
          body: { languageCode: "cn", status: 0 }
        })
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.logisticsList = data.body.logisticsTypes;
          }
        });
    },

    export_smt() {
        this.$confirm('','' ,{
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'success'
        }).then(()=>{
             document.getElementById('form-download').submit();
        }).catch(()=>{
            this.$message.info('');
        })    
    },

    //
    doAction(row) {
      this.remarkFrom = {
        remark: "",
        cancelReason: "",
        primarkIds: []
      };
      this.remarkFrom.remark = row.remark;
      this.remarkFrom.cancelReason = row.cancelReason;
      this.remarkFrom.primarkIds = [
        { orderCode: row.orderCode, packageCode: row.packageCode }
      ];
      this.dialogRemark = true;
    },
    submit_remark() {
      this.axios
        .post("/proxy/report/api/customer/update/logistics", {
          body: this.remarkFrom
        })
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.dialogRemark = false;
            this.getReportList();
            this.$message({
              type: "success",
              message: ""
            });
          }
        });
    }
  },
  created() {
    this.getCpaList();
    this.getLogistics();
    this.getReportList();
  },
  
};
</script>




