<template>
    <div class="wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!---->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <!-- <i class="el-icon-search"></i> -->
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">{{fold?'':''}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                       <el-form-item label="">
                            <el-input v-model="searchForm.packageCode"  size="small"></el-input>
                       </el-form-item>
                       <el-form-item label="">
                            <el-input v-model="searchForm.settlementCode" placeholder="" size="small"></el-input>
                       </el-form-item>
                       <el-form-item label="">
                            <el-select v-model="searchForm.packageStatus" size="small" clearable>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.packageStatus" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.orderCode" placeholder="" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.settlementStatus" size="small" clearable>
                                
                                <el-option value="1" label=""></el-option>
                                <el-option value="2" label=""></el-option>
                                <el-option value="3" label=""></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.goodsSn" placeholder="" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.supplierCode" placeholder="" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.companyName" placeholder="" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.orderSource" placeholder="" size="small" clearable>
                                
                                <el-option :value="item.code" :label="item.name" v-for="item in cpaList" :key="item.code"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.orderTimeStart" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="" class="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.orderTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.paymentTimeStart" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="" class="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.paymentTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt"></el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>
                            </el-button>
                        </el-form-item>
                    </div>
                </transition>

            </el-form>
        </div>
        <!---->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border>
            <el-table-column type="index" width="80" fixed="left"></el-table-column>        
            <el-table-column label="" prop="orderTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.orderTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="" prop="paymentTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.paymentTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="" prop="packageCode" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="packageStatus" align="center" width="120" :show-overflow-tooltip="true" :formatter="format_status"></el-table-column>
            <el-table-column label="" prop="orderCode" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="settlementStatus" align="center" width="100">
                <template slot-scope="scope">{{scope.row.settlementStatus|renderStatus }}</template>
            </el-table-column>
            <el-table-column label="" prop="paymentChannelCode" align="center" width="150"></el-table-column>
            <el-table-column label="" prop="documentsType" align="center" width="100">
                <template slot-scope="scope">{{scope.row.documentsType|documentsType }}</template>
            </el-table-column>
            <el-table-column label="" prop="orderSourceCode" align="center" width="100" :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column label="" prop="goodsSn" align="center" width="180" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="" prop="supplierCode" align="center" width="180" :show-overflow-tooltip="true">  </el-table-column>
            <el-table-column label="" prop="companyName" align="center" width="150" :show-overflow-tooltip="true" ></el-table-column>
            <el-table-column label="" prop="oneCatName" align="center" width="180" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="" prop="twoCatName" align="center" width="120" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="" prop="threeCatName" align="center" width="120" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="" prop="brandName" align="center" width="120" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="" prop="price" align="right" width="100"> 
                 <template slot-scope="scope">{{(scope.row.price/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" prop="qty" align="center" width="120"> </el-table-column>
            <el-table-column label="(USD)" prop="skuFreight" align="left" width="120">
              <template slot-scope="scope">{{scope.row.skuFreight/100}}</template>
            </el-table-column>
            <el-table-column label="" prop="totalPrice" align="right" width="120">
                <template slot-scope="scope">{{(scope.row.totalPrice/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" prop="commissionRate" align="right" width="120"> 
                <template slot-scope="scope">{{(scope.row.commissionRate).toFixed(2)}}%</template>
            </el-table-column>
            <el-table-column label="" prop="commission" align="right" width="120">
                <template slot-scope="scope">{{(scope.row.commission/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" prop="actualPayment" align="right" width="120"> 
                 <template slot-scope="scope">{{(scope.row.actualPayment/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" prop="settlementCode" align="center" width="100"></el-table-column>
            <el-table-column label="" min-width="100">
                <template slot-scope="scope">
                    <el-button size="small" type="primary" @click="doAction(scope.row.acceptUser)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
  padding-right: 40px;
}
</style>

<script>
import format from "date-format";
export default {
  data() {
    return {
      //
      fold: true,
      searchForm: {
        orderTimeStart: "",
        orderTimeEnd: "",
        paymentTimeStart: "",
        paymentTimeEnd: "",
        orderCode: "",
        settlementStatus: "",
        goodsSn: "",
        supplierCode: "",
        companyName: "",
        orderSource: "",
        companyName: "",
        packageStatus:'',
        settlementCode:'',
        page: {
          page: 1,
          pageSize: this.myConfig.pageRows
        }
      },
      pickerOptions: {
        disabledDate(time) {
          if (time.getTime() > Date.now()) {
            return time;
          } else {
            return false;
          }
        }
      },
      //
      loading: false,
      total: 0,
      reportList: [],
      // 
      cpaList: []
    };
  },
  filters: {
    renderStatus(status) {
      switch (status) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 0:
          return "";
          break;
      }
    },
    dateFormat(date) {
      return date ? format("yyyy-MM-dd hh:mm", new Date(date)) : "";
    },
    documentsType(data) {
      switch (data) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 0:
          return "";
          break;
      }
    }
  },
  methods: {
    //
    format_status(row, column, cellValue) {
        for (let i in this.myConfig.status[column.property]) {
            if (this.myConfig.status[column.property][i].val == row[column.property]) {
                return this.myConfig.status[column.property][i].label;
                break;
            }
        }
        return row[column.property];
    },
    DateToString() {
      for (let i in this.searchForm) {
        //
        if (this.searchForm[i] instanceof Date) {
          this.searchForm[i] = format(
            "yyyy-MM-dd",
            new Date(this.searchForm[i])
          );
        }
        //
        if (this.searchForm[i] instanceof String) {
          this.searchForm[i] = this.searchForm[i].trim();
        }
      }
    },
    search_smt() {
      this.searchForm.page.page = 1;
      this.DateToString();
      this.getReportList();
    },
    //
    getReportList() {
      this.loading = true;
      this.axios
        .post(
          this.myConfig.code.proxy + "/report/api/report/financial/commission",
          { body: this.searchForm }
        )
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.loading = false;
            this.reportList = data.body.rows;
            this.total = data.body.total;
          }
        });
    },
    //
    handleCurrentChange(val) {
      this.searchForm.page.page = val;
      this.getReportList();
    },
    doAction(data) {},
    getCpaList() {
      this.axios
        .post(this.myConfig.code.proxy + "/ec/cpa/partners/all", { body: {} })
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.cpaList = data.body.list;
          }
        });
    },
    //
    export_smt() {
      this.DateToString();
      var tmp = JSON.parse(JSON.stringify(this.searchForm));
      delete tmp.page;
      function serialize(obj) {
        var str = [];
        for (var p in obj) {
          if (obj.hasOwnProperty(p) && obj[p] != "") {
            str.push(p + "=" + obj[p]);
          }
        }
        return str.join("&");
      }
    
         window.open("/proxy/report/api/export/financial/commission?" + serialize(tmp));
    }
  },
  created() {
    this.getCpaList();
    this.getReportList();
  }
};
</script>




