<template>
    <div class="wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item>SKU</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!---->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">{{fold?'':''}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="">
                            <el-input v-model="searchForm.supplierCode" placeholder="" size="small"></el-input>
                        </el-form-item>
                        
                        <el-form-item label="">
                            <el-input v-model="searchForm.brandName" placeholder="" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="SPU">
                            <el-input v-model="searchForm.goodsSn" placeholder="SPU" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="SKU">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.addTimeStart" ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="SKU" class="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.addTimeEnd" ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.auditStatus" placeholder="" size="small">
                                <el-option value="" label=""></el-option>
                                <el-option value="1" label=""></el-option>
                                <el-option value="2" label=""></el-option>
                                <el-option value="3" label=""></el-option>
                                <el-option value="5" label=""></el-option>
                                <el-option value="6" label=""></el-option>
                                <el-option value="7" label=""></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.isQty" placeholder="" size="small">
                                <el-option value="" label=""></el-option>
                                <el-option value="0" label=""></el-option>
                                <el-option value="1" label=""></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.isOnSale" placeholder="" size="small">
                                <el-option value="" label=""></el-option>
                                <el-option value="0" label=""></el-option>
                                <el-option value="1" label=""></el-option>
                            </el-select>
                        </el-form-item>

                        <el-form-item label="">
                            <el-select v-model="searchForm.orderSource" placeholder="" size="small">
                                <el-option value="" label=""></el-option>
                                <el-option :value="item.code" :label="item.name" v-for="(item,key) in cpaList" :key="item.code"></el-option>
                            </el-select>
                        </el-form-item>

                        <el-form-item label="SKU">
                            <el-input v-model="searchForm.productSn" placeholder="SKU" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-cascader placeholder="" :options="cateList" change-on-select @change="cateChange" :props="props" filterable :show-all-levels="false" :clearable="true" v-model="searchForm.cateSelect"></el-cascader>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt"></el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>
                            </el-button>

                        </el-form-item>

                    </div>
                </transition>

            </el-form>
        </div>
        <!---->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border>
            <el-table-column label="skuId" prop="productId" align="center" width="80"></el-table-column>
            <el-table-column label="" prop="productSn" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="productPrice" align="right" width="120">
                <template slot-scope="scope">{{(scope.row.productPrice/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" prop="productNumber" align="center" width="80"></el-table-column>
            <el-table-column label="" prop="productTitle" align="center" width="220"></el-table-column>
            <el-table-column label="" prop="addTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.parent.addTime|dateFormat}}</template>
            </el-table-column>
            <el-table-column label="" prop="parent.supplierName" align="center" width="220"></el-table-column>
            <el-table-column label="" prop="parent.oneCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="parent.twoCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="parent.threeCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="parent.fourCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="parent.goodsName" align="center" width="300" show-overflow-tooltip></el-table-column>
            <el-table-column label="SPU" prop="parent.goodsSn" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="parent.brandName" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="isOnSale" align="center" width="120">
                <template slot-scope="scope">{{scope.row.parent.isOnSale==1?"":""}}</template>
            </el-table-column>
            <el-table-column label="SPU" prop="marketPrice" align="right" width="120">
                <template slot-scope="scope">{{(scope.row.parent.marketPrice/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" prop="auditStatus" align="center" width="180">
                <template slot-scope="scope">{{scope.row.parent.auditStatus|auditStatus}}</template>
            </el-table-column>
            <el-table-column label="/" prop="saleTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.parent.saleTime|dateFormat }}</template>
            </el-table-column>

        </el-table>
        <!--  -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            //
            fold: true,
            searchForm: {
                supplierCode: '',
                goodsSn: '',
                auditStatus: '',
                brandId: '',
                brandName: '',
                isQty: '',
                isOnSale: '',
                cateSelect: [],
                oneCatId: '',
                twoCatId: '',
                threeCatId: '',
                fourCatId: '',
                addTimeStart:'',
                addTimeEnd:'',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },

            //
            loading: false,
            total: 0,
            reportList: [],
            // 
            cpaList: [],
            //
            cateList: [],
            props: {
                value: 'catId',
                label: 'catName'
            }

        }
    },
    filters: {
        auditStatus(status) {
            switch (status) {
                case 1:
                    return '';
                    break;
                case 2:
                    return '';
                    break;
                case 3:
                    return '';
                    break;
                case 5:
                    return '';
                    break;
                case 6:
                    return '';
                    break;
                case 7:
                    return '';
                    break;
                default:
                    return '';
                    break;
            }
        },
        dateFormat(date) {
            return date ? format('yyyy-MM-dd hh:mm', new Date(date)) : '';
        }
    },
    methods: {
        DateToString() {
            for (let i in this.searchForm) {
                //
                if (this.searchForm[i] instanceof Date) {                 
                    this.searchForm[i] = format('yyyy-MM-dd', new Date(this.searchForm[i]));       
                }
                //
                if (this.searchForm[i] instanceof String) {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
        },
        search_smt() {
            this.searchForm.page.page = 1;
            this.DateToString();
            this.getReportList();
        },
        //
        getReportList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/report/api/report/operation/product', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.reportList = data.body.rows;
                    this.total = data.body.total;
                }
            })
        },
        //
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getReportList();
        },
        //cpa
        getCpaList() {
            this.axios.post(this.myConfig.code.proxy + '/ec/cpa/partners/all', { body: {} }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.cpaList = data.body.list;
                }
            })

        },
        //
        getCateList() {
            this.axios.post(this.myConfig.code.proxy + '/report/api/common/category/options', { body: {} }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.cateList = data.body;
                }
            })
        },
        export_smt() {
            var tmp = JSON.parse(JSON.stringify(this.searchForm));
            delete tmp.page;
            function serialize(obj) {
                var str = [];
                for (var p in obj)
                    if (obj.hasOwnProperty(p) && obj[p] != "") {
                        str.push(p + "=" + obj[p]);
                    }
                return str.join("&");
            }
             window.open('/proxy/report/api/export/operation/product?' + serialize(tmp));
        },

        //
        cateChange(change) {
            this.searchForm.oneCatId = '';
            this.searchForm.twoCatId = '';
            this.searchForm.threeCatId = '';
            this.searchForm.fourCatId = '';
            for (let [index, item] of this.searchForm.cateSelect.entries()) {
                switch (index) {
                    case 0:
                        this.searchForm.oneCatId = item;
                        break;
                    case 1:
                        this.searchForm.twoCatId = item;
                        break;
                    case 2:
                        this.searchForm.threeCatId = item;
                        break;
                    case 3:
                        this.searchForm.fourCatId = item;
                        break;
                }
            }
        }

    },
    created() {
        this.getCpaList();
        this.getCateList();
        this.getReportList();
    }
}
</script>




