<template>
    <div class="wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item>SPU</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!---->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">{{fold?'':''}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="">
                            <el-input v-model="searchForm.supplierCode" placeholder="" size="small"></el-input>
                        </el-form-item>      
                        <el-form-item label="spu">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.addTimeStart" ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="spu" class="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.addTimeEnd" ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.isOnSale" placeholder="" size="small" clearable>                      
                                <el-option value="0" label=""></el-option>
                                <el-option value="1" label=""></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="/">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.onSaleTimeStart" ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="/" class="">
                            <el-date-picker type="date" placeholder="" v-model="searchForm.onSaleTimeEnd" ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="searchForm.brandName" placeholder="" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.auditStatus" placeholder="" size="small" clearable>          
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.auditStatus" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="searchForm.isQty" placeholder="" size="small" clearable>                       
                                <el-option value="0" label=""></el-option>
                                <el-option value="1" label=""></el-option>
                            </el-select>
                        </el-form-item>
                        
                        <el-form-item label="">
                            <el-cascader placeholder="" :options="cateList" change-on-select @change="cateChange" :props="props" filterable :show-all-levels="false" clearable></el-cascader>
                        </el-form-item>
                        <el-form-item label="SPU">
                            <el-input v-model="searchForm.goodsSn" :rows="1"  type="textarea" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="SPU ID">
                            <el-input v-model="searchForm.goodsId"  :rows="1" type="textarea" size="small"></el-input>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt"></el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>
                            </el-button>
                        </el-form-item>
                    </div>
                </transition>

            </el-form>
        </div>
        <!---->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border>
            <el-table-column label="" prop="supplierCode" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="supplierName" align="center" width="220" show-overflow-tooltip></el-table-column>
            <!-- <el-table-column label="" prop="logisticsType" align="center" width="220" show-overflow-tooltip :formatter="format_status"></el-table-column> -->
            <el-table-column label="SPU ID" prop="goodsId" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="oneCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="twoCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="threeCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="fourCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="goodsName" align="center" width="300" show-overflow-tooltip></el-table-column>
            <el-table-column label="SPU" prop="goodsSn" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="brandName" align="center" width="180"></el-table-column>
            <el-table-column label="" prop="isQty" align="center" width="120">
                <template slot-scope="scope">{{scope.row.isQty==1?"":""}}</template>
            </el-table-column>
            <el-table-column label="" prop="isOnSale" align="center" width="120">
                <template slot-scope="scope">{{scope.row.isOnSale==1?"":""}}</template>
            </el-table-column>
            <el-table-column label="" prop="marketPrice" align="right" width="100">
                <template slot-scope="scope">{{(scope.row.marketPrice/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" prop="shopPrice" align="right" width="100">
                <template slot-scope="scope">{{(scope.row.shopPrice/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="" prop="auditStatus" align="center" width="180" :formatter="format_status">
        
            </el-table-column>
            <el-table-column label="SPU" prop="stock" align="center" width="100"></el-table-column>
            <el-table-column label="sku" prop="skuNum" align="center" width="100"></el-table-column>
            <el-table-column label="/" prop="saleTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.saleTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="spu" prop="addTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.addTime|dateFormat }}</template>
            </el-table-column>

        </el-table>
        <!--  -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
  padding-right: 40px;
}
</style>

<script>
import format from "date-format";
export default {
  data() {
    return {
      //
      fold: true,
      searchForm: {
        supplierCode: "",
        goodsSn: "",
        auditStatus: "",
        brandId: "",
        brandName: "",
        isQty: "",
        isOnSale: "",
        oneCatId: "",
        twoCatId: "",
        threeCatId: "",
        fourCatId: "",
        addTimeStart:'',
        addTimeEnd:'',
        onSaleTimeStart:'',
        onSaleTimeEnd:'',
        page: {
          page: 1,
          pageSize: this.myConfig.pageRows
        }
      },

      //
      loading: false,
      total: 0,
      reportList: [],
      // 
      cpaList: [],
      //
      cateList: [],
      props: {
        value: "catId",
        label: "catName"
      }
    };
  },
  filters: {
    dateFormat(date) {
      return date ? format("yyyy-MM-dd hh:mm", new Date(date)) : "";
    }
  },
  methods: {
    //
    format_status(row, column, cellValue) {
      for (let i in this.myConfig.status[column.property]) {
        if (
          this.myConfig.status[column.property][i].val == row[column.property]
        ) {
          return this.myConfig.status[column.property][i].label;
          break;
        }
      }
      return row[column.property];
    },
    DateToString() {
      for (let i in this.searchForm) {
          //
          if (this.searchForm[i] instanceof Date) {        
                this.searchForm[i] = format('yyyy-MM-dd', new Date(this.searchForm[i]));  
          }
          //
          if (this.searchForm[i] instanceof String) {
              this.searchForm[i] = this.searchForm[i].trim();
          }
      }
    },
    search_smt() {
      this.searchForm.page.page = 1;
      this.DateToString();
      this.getReportList();
    },
    //
    getReportList() {
      this.loading = true;
      this.axios
        .post(this.myConfig.code.proxy + "/report/api/report/operation/goods", {
          body: this.searchForm
        })
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.loading = false;
            this.reportList = data.body.rows;
            this.total = data.body.total;
          }
        });
    },
    //
    handleCurrentChange(val) {
      this.searchForm.page.page = val;
      this.getReportList();
    },
    //cpa
    getCpaList() {
      this.axios
        .post(this.myConfig.code.proxy + "/ec/cpa/partners/all", { body: {} })
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.cpaList = data.body.list;
          }
        });
    },
    //
    getCateList() {
      this.axios
        .post(
          this.myConfig.code.proxy + "/report/api/common/category/options",
          { body: {} }
        )
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.cateList = data.body;
          }
        });
    },
    export_smt() {
      var tmp = JSON.parse(JSON.stringify(this.searchForm));
      delete tmp.page;
      function serialize(obj) {
        var str = [];
        for (var p in obj)
          if (obj.hasOwnProperty(p) && obj[p] != "") {
            str.push(p + "=" + encodeURIComponent(obj[p]));
          }
        return str.join("&");
      }
      
         window.open("/proxy/report/api/export/operation/goods?" + serialize(tmp));
    },

    //
    cateChange(change) {
      this.searchForm.oneCatId = "";
      this.searchForm.twoCatId = "";
      this.searchForm.threeCatId = "";
      this.searchForm.fourCatId = "";
      for (let [index, item] of change.entries()) {
        switch (index) {
          case 0:
            this.searchForm.oneCatId = item;
            break;
          case 1:
            this.searchForm.twoCatId = item;
            break;
          case 2:
            this.searchForm.threeCatId = item;
            break;
          case 3:
            this.searchForm.fourCatId = item;
            break;
        }
      }
    }
  },
  created() {
    this.getCpaList();
    this.getCateList();
    this.getReportList();
  }
};
</script>




